import { Component, OnInit } from '@angular/core';
import {UserLoginService} from '../../service/userlogin.service';
import {Router} from '@angular/router';
import {CartService} from '../../service/cart.service';
import {UserService} from '../../service/user.service';
import {ToastrService} from 'ngx-toastr';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
@Component({
  selector: 'app-user-panel',
  templateUrl: './user-panel.component.html',
  styleUrls: ['./user-panel.component.css']
})
export class UserPanelComponent implements OnInit {
   Cart:any;
   profileForm:FormGroup;
   Order:any;
  constructor(private service:UserLoginService,private router:Router,private cartService:CartService,private toastr:ToastrService,private uService:UserService,private formBuilder:FormBuilder) { }

  ngOnInit() {
    this.loadCart();
    this.createForm();
    this.loadForm();
  }
   logout()
   {
     this.service.logout();
     this.router.navigate(['/login']);
   }
   createForm()
   {
     this.profileForm=this.formBuilder.group({
       id:['',Validators.required],
       name:['',Validators.required],
       email:['',Validators.required],
       phone:['',Validators.required]
     });
   }
   loadForm()
   {
    const email= this.service.currentUserValue.email;
    this.loadData(email);
    this.getOrdersDetails(email);
   }
   loadData(email)
   {
     this.uService.getUserdetails(email).subscribe(res=>{
        this.profileForm.setValue({
          id:res.user.id,
          name:res.user.Name,
          email:res.user.Email,
          phone:res.user.Phone
        });
     });
   }
   loadCart()
   {
     this.cartService.getProducts().subscribe(res=>{
       this.Cart=res;
     },err=>{
       this.toastr.success('Please try again','Error occured');
     });
   }
   navigateCart()
   {
     this.router.navigate(['/cart']);
   }
   updateUserdetails(id,name,email,phone)
   {
     this.uService.updateUser(id,name,email,phone).subscribe(res=>{
       this.toastr.success('User updated','Success');

     },err=>{
       this.toastr.success(err.message,'Error occured');
     });
   }
   getOrdersDetails(email)
   {
     this.uService.getOrders(email).subscribe(res=>{
       this.Order=res;
     },err=>{
       this.toastr.success(err.message,'Error occured');
     });
   }

}
