import { Component, OnInit,Inject } from '@angular/core';
import {CartService} from '../../service/cart.service';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import {FormBuilder,FormGroup,Validators} from '@angular/forms';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-cart-form-modal',
  templateUrl: './cart-form-modal.component.html',
  styleUrls: ['./cart-form-modal.component.css']
})
export class CartFormModalComponent implements OnInit {
   updateCartForm:FormGroup;
  constructor(private service:CartService,private formBuilder:FormBuilder,public cartFormDialogref:MatDialogRef<CartFormModalComponent>,@Inject(MAT_DIALOG_DATA) public data:string,private toastr:ToastrService) { }

  ngOnInit() {
    this.createForm();
    this.getProductdetails(this.data);
  }
  getProductdetails(data)
  {
    this.service.getProduct(data).subscribe(res=>{
       this.updateCartForm.setValue({
         name:res.result.Name,
         price:res.result.Price,
         quantity:res.result.Quantity,
         cost:res.result.Cost
       })
    },err=>{

    });
  }
  createForm()
  {
    this.updateCartForm=this.formBuilder.group({
      name:['',Validators.required],
      price:['',Validators.required],
      quantity:['',Validators.required],
      cost:['',Validators.required]


    });
  }
  updateCost()
  {
    this.updateCartForm.get('cost').setValue((this.updateCartForm.get('quantity').value*this.updateCartForm.get('price').value).toFixed(2));
  }
  resetForm()
  {
    this.updateCartForm.reset();
  }
  updateCart(name,price,quantity,cost)
  {
    this.service.updateproduct(name,price,quantity,cost).subscribe(res=>{
      this.toastr.success('Cart updated','');
      this.resetForm();
      this.cartFormDialogref.close();
    },err=>{
      this.toastr.success('Error occured','Please try again');
      this.resetForm();
      this.cartFormDialogref.close();
    });
  }
}
