import { Component, OnInit } from '@angular/core';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {AdminService} from '../../service/admin.service';
import {Router,ActivatedRoute} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-admin-register',
  templateUrl: './admin-register.component.html',
  styleUrls: ['./admin-register.component.css']
})
export class AdminRegisterComponent implements OnInit {
   registerForm:FormGroup
  constructor(private router:Router,private service:AdminService, private formBuilder:FormBuilder,private toastr:ToastrService) { }

  ngOnInit() {
    this.createForm();
  }
  createForm()
  {
    this.registerForm=this.formBuilder.group({
      username:['',Validators.required],
      password:['',Validators.required]
    });
  }
  resetForm()
  {
    this.registerForm.reset();
  }
  registerAdmin(username,password)
  {
    this.service.registerAdmin(username,password).subscribe(res=>{
      this.toastr.success('User registered','Please login to continue');
      this.resetForm();
    },err=>{
      this.toastr.success('Error occured',err);
      this.resetForm();
    });
  }
}
