import { Component, OnInit,Inject } from '@angular/core';
import {ProductService} from '../../service/product.service';
import {CartService} from '../../service/cart.service';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-cart-modal',
  templateUrl: './cart-modal.component.html',
  styleUrls: ['./cart-modal.component.css']
})
export class CartModalComponent implements OnInit {
   Product:any;
   orderForm:FormGroup;
  constructor(private service:ProductService,private formBuilder:FormBuilder,public cartDialogref:MatDialogRef<CartModalComponent>,@Inject(MAT_DIALOG_DATA) public data:string,private toastr:ToastrService,private cService:CartService) { }

  ngOnInit() {
    this.createForm();
    this.getProductDetails(this.data);
  }
  getProductDetails(data:string)
  {
    this.service.getproduct(data).subscribe(data=>{
        this.orderForm.setValue({
          name:data.product.Productname,
          quantity:"1",
          price:data.product.Productcost,
          cost:data.product.Productcost
        });
      },err=>{
            alert(err);
    });
  }
  createForm()
  {
    this.orderForm=this.formBuilder.group({
      name:['',Validators.required],
      quantity:['',Validators.required],
      price:['',Validators.required],
      cost:['',Validators.required]
    });
  }
  resetForm()
  {
    this.orderForm.reset();
  }
  updateCost()
  {
    this.orderForm.get('cost').setValue((this.orderForm.get('quantity').value*this.orderForm.get('price').value).toFixed(2));
  }
  addProduct(name,price,quantity,cost)
  {
    this.cService.addproduct(name,quantity,price,cost).subscribe(res=>{
         this.resetForm();
         this.toastr.success('Product Added to cart','Product added');
         this.cartDialogref.close();
    },err=>{
         this.toastr.success('Error occured please try again','Oops error occurred');
    });
  }
}
