import { Component, OnInit } from '@angular/core';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {CartService} from '../../service/cart.service';
import {ToastrService} from 'ngx-toastr';
import{UserLoginService} from '../../service/userlogin.service';
import{Router} from '@angular/router';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {
   checkoutForm:FormGroup;
   orderSummary:Array<string>=[];
   grandTotal:any;
   orderDate:any;
   date:any;
  constructor(private formBuilder:FormBuilder,private service:CartService,private toastr:ToastrService,private uservice:UserLoginService,private router:Router) { }

  ngOnInit() {
    this.createForm();
    this.generateUserDetails();
    this.generateCart();
    this.date=new Date();
  }
   createForm()
   {
     this.checkoutForm=this.formBuilder.group({
       name:['',Validators.required],
       phone:['',Validators.required],
       email:['',Validators.required],
       address:['',Validators.required]
     });
   }
   resetForm()
   {
     this.checkoutForm.reset();
   }
   generateUserDetails()
   {
     const details=this.uservice.currentUserValue;
     this.checkoutForm.setValue({
       name:details.name,
       phone:details.phone,
       email:details.email,
       address:""
     });

   }
   generateCart()
   {
     this.service.getProducts().subscribe(res=>{
         this.generateTotal(res);
         this.generateOrderSummary(res);
     });
   }
   generateTotal(res)
   {
       this.grandTotal=res.cart.reduce((acc,{Cost})=>acc+Cost,0);
       console.log(this.grandTotal);
   }
   generateOrderSummary(res)
   {

      for(let i=0;i<res.cart.length;i++)
      {
        this.orderSummary.push(res.cart[i].Quantity+" x "+res.cart[i].Name);
      }
       console.log(this.orderSummary.toString());
   }
   PlaceOrder(name,phone,email,address)
   {

         const orderDate=this.date.getDate()+'/'+parseInt(this.date.getMonth()+1)+'/'+this.date.getFullYear();
         const ordersummary=this.orderSummary.toString();
         const totalcost=this.grandTotal;
         this.service.placeOrder(name,phone,email,address,ordersummary,orderDate,totalcost).subscribe(res=>{
          if(address===null)
          {
            this.toastr.success('error occured','Invalid address');
           }
           this.toastr.success('Succesful','Order placed');
           this.resetForm();
           this.router.navigate(['/']);
         },err=>{
            this.toastr.success('error occured','Invalid address');
         });

   }
}
