import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {BehaviorSubject,Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Admin} from '../models/Admin.model';
@Injectable({
  providedIn:'root'
})
export class AdminLoginService{
  readonly apiUrl='http://localhost:8080/admin/';
  private currentAdminSubject:BehaviorSubject<Admin>;
  private currentAdmin:Observable<Admin>
  constructor(private http:HttpClient)
  {
     this.currentAdminSubject=new BehaviorSubject<Admin>(JSON.parse(localStorage.getItem('currentAdmin')));
     this.currentAdmin=this.currentAdminSubject.asObservable();
  }
  public get currentAdminValue():Admin{
    return this.currentAdminSubject.value;
  }
  loginAdmin(username,password)
  {
    const AdminData={
      username:username,
      password:password
    }
    return this.http.post<any>(this.apiUrl+'login',AdminData).pipe(map(admin=>{
      if(admin&&admin.token)
      {
        localStorage.setItem('currentAdmin',JSON.stringify(admin));
        this.currentAdminSubject.next(admin);
      }
      return admin;
    }));
  }
  logout()
  {
    localStorage.removeItem('currentAdmin');
    this.currentAdminSubject.next(null);
  }
}
