import { Component, OnInit,Inject } from '@angular/core';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import {FormBuilder,FormGroup,Validators} from '@angular/forms';
import {AdminService} from '../../service/admin.service';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-admin-order',
  templateUrl: './admin-order.component.html',
  styleUrls: ['./admin-order.component.css']
})
export class AdminOrderComponent implements OnInit {
  updateOrderform:FormGroup;
  constructor(private service:AdminService,private formBuilder:FormBuilder,public orderDialogref:MatDialogRef<AdminOrderComponent>,@Inject(MAT_DIALOG_DATA) public data:string,private toastr:ToastrService) { }

  ngOnInit() {
     this.createForm();
     this.populateForm(this.data);
  }
   populateForm(data)
   {
    this.service.getOrderDetails(data).subscribe(res=>{
        this.updateOrderform.setValue({
          id:res.result.id,
          name:res.result.Name,
          email:res.result.Email,
          phone:res.result.Phone,
          ordersummary:res.result.OrderSummary,
          orderdate:res.result.Orderdate,
          orderstatus:res.result.Orderstatus
        });
    });
   }
   createForm()
   {
     this.updateOrderform=this.formBuilder.group({
       id:['',Validators.required],
       name:['',Validators.required],
       email:['',Validators.required],
       phone:['',Validators.required],
       ordersummary:['',Validators.required],
       orderdate:['',Validators.required],
       orderstatus:['',Validators.required]
     });
   }
   resetForm()
   {
     this.updateOrderform.reset();
   }
   updateOrder(id,status)
   {
     this.service.updateOrder(id,status).subscribe(res=>{
                this.toastr.success('Status updated',status);
               this.resetForm();
               this.orderDialogref.close();

     },err=>{
         this.toastr.success('error occured',err);
         this.resetForm();
         this.orderDialogref.close();
     });

   }
   cancelUpdate()
   {
     this.orderDialogref.close();
   }
}
