import { Component, OnInit } from '@angular/core';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {AdminLoginService} from '../../service/adminlogin.service';;
import {Router,ActivatedRoute} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.css']
})
export class AdminLoginComponent implements OnInit {
   loginForm:FormGroup;
   returnUrl:string;

  constructor(private router:Router,private service:AdminLoginService,private toastr:ToastrService,private formBuilder:FormBuilder,
  private route:ActivatedRoute ) {
    if(this.service.currentAdminValue)
    {
      this.router.navigate(['/admin/panel']);

    }
   }

  ngOnInit() {
    this.createForm();
      this.returnUrl=this.route.snapshot.queryParams['returnUrl']||'/admin/panel';
  }
   createForm()
   {
     this.loginForm=this.formBuilder.group({
       username:['',Validators.required],
       password:['',Validators.required]
     });
   }
   resetForm()
   {
     this.loginForm.reset();
   }
   adminLogin(username,password)
   {
     this.service.loginAdmin(username,password).subscribe(res=>{
       this.router.navigate([this.returnUrl]);
       this.resetForm();
     },err=>{
       this.toastr.success('Error occured','Inavlid username or password');
       this.resetForm();
     });
   }
}
