import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Cart} from '../models/Cart.model';
@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor(private http:HttpClient) { }
  readonly apiUrl='http://localhost:8080/cart/';

  addproduct(name,price,quantity,cost)
  {
    const cartData={
      name:name,
      price:price,
      quantity:quantity,
      cost:cost
    }
    return this.http.post<any>(this.apiUrl+'addproduct',cartData);
  }
  updateproduct(name,price,quantity,cost)
  {
    const cartData={
      price:price,
      quantity:quantity,
      cost:cost
    }
    return this.http.put<any>(this.apiUrl+'updateproduct/'+name,cartData);
  }
  getProducts()
  {
    return this.http.get<Cart[]>(this.apiUrl);
  }
  deleteProduct(id:string)
  {
    return this.http.delete<any>(this.apiUrl+'deleteproduct/'+id);
  }
  getProduct(data:string)
  {
    return this.http.get<any>(this.apiUrl+data);
  }
  placeOrder(name,phone,email,address,ordersummary,orderdate,ordercost)
  {
    const orderData={
      name:name,
      phone:phone,
      email:email,
      address:address,
      ordersummary:ordersummary,
      orderdate:orderdate,
      ordercost:ordercost
    }
    return this.http.post(this.apiUrl+'placeorder',orderData);
  }
}
