import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';
import {User} from '../models/User.model';
import {UserOrder} from '../models/UserOrder.model';
@Injectable({
  providedIn:'root'
})
export class UserService{
    readonly apiUrl='http://localhost:8080/user/';
  constructor(private http:HttpClient)
  {

  }
  registerUser(name,email,password,phone)
  {
    const userData={
      name:name,
      email:email,
      password:password,
      phone:phone
    }
    return this.http.post<any>(this.apiUrl+'register',userData);
  }
   getUserdetails(email:string)
   {
     return this.http.get<any>(this.apiUrl+'details/'+email);
   }
   updateUser(id,name,email,phone)
   {
     const userData={
       name:name,
       email:email,
       phone:phone
     }
     return this.http.put<any>(this.apiUrl+id,userData);
   }
   getOrders(email)
   {
     return this.http.get<UserOrder[]>(this.apiUrl+'order/'+email);
   }
}
