import { Component, OnInit } from '@angular/core';
import {ProductService} from '../../service/product.service';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import {ToastrService} from 'ngx-toastr';
import {CartModalComponent} from '../cart-modal/cart-modal.component';
import {Router} from '@angular/router';
import {UserLoginService} from '../../service/userlogin.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  product:any;
  cartquantity:any;
  constructor(private uService:UserLoginService,private service:ProductService,private router:Router,public dialog:MatDialog,private toastr:ToastrService) { }

  ngOnInit() {
   this.getProducts();
   this.cartquantity=0;
  }
  getProducts()
  {
    this.service.getProducts().subscribe(res=>{
      this.product=res;
    });
  }
  openCartModal(id)
  {
    let dialogRef=this.dialog.open(CartModalComponent,{
      width:'600px',
      data:id
    }).afterClosed().subscribe(res=>{
      this.cartquantity=this.cartquantity+1;
    },err=>{
      this.cartquantity=this.cartquantity+0;
    });
  }
  cartDirect()
  {
     this.router.navigate(['/cart']);
  }
}
