import { Component, OnInit } from '@angular/core';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {UserLoginService} from '../../service/userlogin.service';
import {Router,ActivatedRoute} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-user-login',
  templateUrl: './user-login.component.html',
  styleUrls: ['./user-login.component.css']
})
export class UserLoginComponent implements OnInit {
   loginForm:FormGroup;
   returnUrl:string;

  constructor(private service:UserLoginService,private router:Router,private formBuilder:FormBuilder,private route:ActivatedRoute,private toastr:ToastrService) {
    if(this.service.currentUserValue)
    {
      this.router.navigate(['/panel']);
    }

   }

  ngOnInit() {
    this.createForm();
    this.returnUrl=this.route.snapshot.queryParams['returnUrl']||'/panel';
  }
  createForm()
  {
    this.loginForm=this.formBuilder.group({
      email:['',Validators.required],
      password:['',Validators.required]
    });
  }
  resetForm()
  {
    this.loginForm.reset();
  }
  userLogin(email,password)
  {
    this.service.loginUser(email,password).subscribe(data=>{
      this.router.navigate([this.returnUrl]);
      this.resetForm();
    },err=>{
       this.toastr.success('Error occured','INvalid email or password');
       this.resetForm();
    });
  }
  navigateRegister()
  {
    this.router.navigate(['/register']);
  }
}
