import { Component, OnInit,Inject } from '@angular/core';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {AdminService} from '../../service/admin.service';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-user-modal',
  templateUrl: './user-modal.component.html',
  styleUrls: ['./user-modal.component.css']
})
export class UserModalComponent implements OnInit {
   updateForm:FormGroup;
  constructor(private service:AdminService,private formBuilder:FormBuilder,public userDialogref:MatDialogRef<UserModalComponent>,@Inject(MAT_DIALOG_DATA) public data:string,private toastr:ToastrService) { }

  ngOnInit() {
     this.createForm();
     this.populateForm(this.data);
  }
  populateForm(data:string)
  {
     this.service.getUser(data).subscribe(data=>{
       this.updateForm.setValue({
         id:data.user.id,
         name:data.user.Name,
         email:data.user.Email,
         phone:data.user.Phone
       });
     });
  }
  createForm()
  {
    this.updateForm=this.formBuilder.group({
      id:['',Validators.required],
      name:['',Validators.required],
      email:['',Validators.required],
      phone:['',Validators.required]
    });
  }
  resetForm()
  {
    this.updateForm.reset();
  }
  updateUserDetails(id,name,email,phone)
  {
    this.service.updateUser(id,name,email,phone).subscribe(res=>{
      if(res.message==="User updated")
      {
        this.resetForm();
        this.toastr.success('User','updated');
        this.userDialogref.close();
      }
    },err=>{
      this.resetForm();
      this.toastr.success('Error occured','Please try again');
      this.userDialogref.close();
    });
  }
}
