import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import {Router,CanActivate,ActivatedRouteSnapshot,RouterStateSnapshot} from '@angular/router';
import {AdminLoginService} from '../service/adminlogin.service';
@Injectable({
  providedIn: 'root'
})
export class AdminGuard implements CanActivate {
  constructor(private service:AdminLoginService,private router:Router)
   {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot) {
      const currentAdmin=this.service.currentAdminValue;
      if(currentAdmin)
      {
        return true;
      }
      this.router.navigate(['/admin-panel'],{queryParams:{returnUrl:state.url}});
       return false;
  }
}
