import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {BehaviorSubject,Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {UserModel} from '../models/UserModel.model';
import {Order} from '../models/Order.model';
@Injectable({
  providedIn:'root'
})
export class AdminService{
  readonly apiUrl='http://localhost:8080/admin/';

  constructor(private http:HttpClient)
  {}
  registerAdmin(username,password)
  {
    const adminData={
      username:username,
      password:password
    }
    return this.http.post<any>(this.apiUrl+'register',adminData);
  }
  updateUser(id,name,email,phone)
  {
    const userData={
      name:name,
      email:email,
      phone:phone
    }
    return this.http.put<any>(this.apiUrl+'User/'+id,userData);
  }
  deleteUser(email:string)
  {
    return this.http.delete<any>(this.apiUrl+'User/'+email);
  }
   getUsers()
   {
     return this.http.get<UserModel[]>(this.apiUrl+'Users');
   }
   getUser(data:string)
   {
     return this.http.get<any>(this.apiUrl+'User/'+data);
   }
   getOrder()
   {
     return this.http.get<Order[]>(this.apiUrl+'Orders');
   }
   getOrderDetails(data:string)
   {
     return this.http.get<any>(this.apiUrl+'Order/'+data);
   }
   updateOrder(id:string,status:string)
   {
     const  orderData={
       orderstatus:status
     }
     return this.http.put<any>(this.apiUrl+'Order/'+id,orderData);
   }
   deleteOrder(id:string)
   {
     return this.http.delete<any>(this.apiUrl+'Order/'+id);
   }

}
