import { Component, OnInit } from '@angular/core';
import {CartService} from '../../service/cart.service';
import {ToastrService} from 'ngx-toastr';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import {CartFormModalComponent} from '../cart-form-modal/cart-form-modal.component';
@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
   cartList:any;
   grandTotal:any;
   orderSummary:any;
   date:any;
  constructor(private service:CartService,private toastr:ToastrService,public dialog:MatDialog) { }

  ngOnInit() {
    this.getCart();
    this.date=new Date();

  }
  getCart()
  {
    this.service.getProducts().subscribe(res=>{
      this.cartList=res;
         this.calculateTotal(res);
         this.generateCartData(res);
    });

  }
  calculateTotal(res)
  {
     this.grandTotal=res.cart.reduce((acc,{Cost})=>acc+Cost,0);
  }
  deleteCart(name)
  {
    this.service.deleteProduct(name).subscribe(res=>{
      this.toastr.success('Product deleted','please proceed further');
      this.getCart();
    },err=>{
      this.toastr.success('Error occured','Please try again ');
    });
  }
  updateCart(name)
  {
    let dialogRef=this.dialog.open(CartFormModalComponent,{
      width:'600px',
      data:name
    }).afterClosed().subscribe(res=>{
      this.getCart();
    });
  }
   generateCartData(res)
   {
     let orderSummary:Array<string>=[];
      for(let i=0;i<res.cart.length;i++)
      {
        orderSummary.push(res.cart[i].Quantity+" x "+res.cart[i].Name);
      }
       console.log(orderSummary);
       console.log(this.date.getDate()+'/'+parseInt(this.date.getMonth()+1)+'/'+this.date.getFullYear());
   }
}
