import { Component, OnInit } from '@angular/core';
import {AdminLoginService} from '../../service/adminlogin.service';
import {AdminService} from '../../service/admin.service';
import {Router} from '@angular/router';
import {MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material';
import {UserModalComponent} from '../user-modal/user-modal.component';
import {AdminOrderComponent} from '../admin-order/admin-order.component';
import { ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css']
})
export class AdminPanelComponent implements OnInit {
   customers:any;
   orders:any;
  constructor(private service:AdminLoginService,private router:Router,private aservice:AdminService,public dialog:MatDialog,private toastr:ToastrService) { }

  ngOnInit() {
    this.generatecustomerList();
    this.generateorderList();
  }
  logout()
  {
    this.service.logout();
    this.router.navigate(['/admin/login']);
  }
  generateorderList()
  {
    this.aservice.getOrder().subscribe(res=>{
      this.orders=res;
    })
  }
  generatecustomerList()
  {
    this.aservice.getUsers().pipe().subscribe(res=>{this.customers=res});
  }
  openCustomerDialog(id)
  {
      let dialogRef=this.dialog.open(UserModalComponent,{
        width:'600px',
        data:id
      }).afterClosed().subscribe(res=>{
        this.generatecustomerList();
      });
  }
  openOrderDialog(id)
  {
    let dialogRef=this.dialog.open(AdminOrderComponent,{
      width:'600px',
      data:id
    }).afterClosed().subscribe(res=>{
      this.generateorderList();
    });
  }
  deleteCustomer(email)
  {
    this.aservice.deleteUser(email).subscribe(res=>{
      this.toastr.success('User','deleted');
      this.generatecustomerList();
    },err=>{
      this.toastr.success('Error occured','please try again');
      this.generatecustomerList();
    });
  }
  deleteOrder(id)
  {
    this.aservice.deleteOrder(id).subscribe(res=>{
      this.toastr.success('Deleted','Order');
      this.generateorderList();
    },err=>{
      this.toastr.success(err.message,'Error');
      this.generateorderList();
    });
  }
}
