import { Component, OnInit } from '@angular/core';
import {NgForm} from '@angular/forms';
import {UserService} from '../../service/user.service';
import {FormGroup,FormBuilder,Validators} from '@angular/forms';
import {ToastrService} from 'ngx-toastr';
@Component({
  selector: 'app-user-register',
  templateUrl: './user-register.component.html',
  styleUrls: ['./user-register.component.css']
})
export class UserRegisterComponent implements OnInit {
   registerForm:FormGroup;
  constructor(private service:UserService,private formBuilder:FormBuilder,private toastr:ToastrService) { }

  ngOnInit() {
    this.createForm();
  }
  createForm()
  {
    this.registerForm=this.formBuilder.group({
      name:['',Validators.required],
      email:['',Validators.required],
      password:['',Validators.required],
      phone:['',Validators.required]

    });
  }
  resetForm()
  {
    this.registerForm.reset();
  }
  onSumbit(name,email,password,phone)
  {
    this.service.registerUser(name,email,password,phone).subscribe(res=>{
      this.toastr.success('User Registered','Please proceed to login for continue operations');
      this.resetForm();
    },err=>{
      this.toastr.success('Error occured','Please close for further processing');
      this.resetForm();
    });;
  }
}
